import React, { useState, useEffect, useRef } from 'react';
import SpeechRecognition, { useSpeechRecognition } from 'react-speech-recognition';
import { useNavigate } from 'react-router-dom';
import OpenAI from 'openai';
import { ref, onValue, remove } from 'firebase/database';

import { database } from './Firebase';

const InterviewLayout = () => {
  const navigate = useNavigate()
  const [transcriptLines, setTranscriptLines] = useState([]);  // Stores each transcript line with its timestamp and source type
  const [latestTranscript, setLatestTranscript] = useState(''); // For storing the latest transcript
  const [correctedTranscript, setCorrectedTranscript] = useState(''); // For storing corrected transcript
  const [technology, setTechnology] = useState('');
  const [isListening, setIsListening] = useState(false);
  const [chatInput, setChatInput] = useState('');
  const [manualOrCode, setManualOrCode] = useState('')
  const [code, setCode] = useState(false)
  const videoRef = useRef(null);
  const dataRef = ref(database, `API_KEY`);
  let apiKey = '';
  onValue(dataRef, (snapshot) => {
    const data = snapshot.val();
    if (data) {
      apiKey = data;
    }
  });

  const openai = new OpenAI({
    apiKey: apiKey,
    dangerouslyAllowBrowser: true
  });

  const { transcript, listening, resetTranscript, startListening, stopListening } = useSpeechRecognition();

  useEffect(() => {
    setLatestTranscript(transcript);
    // correctTranscript(transcript, technology)  // Update the latest transcript on every change
  }, [transcript]);

  // Function to correct the transcript using OpenAI API
  const correctTranscript = async (text, technology) => {
    if (!text || !technology) {
      console.log('No transcript or technology provided.');
      return;
    }

    try {
      const res = await fetch('https://api.openai.com/v1/chat/completions', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${apiKey}`,
        },
        body: JSON.stringify({
          model: 'gpt-3.5-turbo',
          messages: [
            { role: 'system', content: 'You are a helpful assistant. Please correct the following transcript for grammatical errors and make it more relevant to the specified technology.' },
            { role: 'user', content: `Correct the following transcript: "${text}" based on the technology: ${technology}.` }
          ],
          temperature: 0.5,
          max_tokens: 300,
        }),
      });

      const jsonResponse = await res.json();
      if (jsonResponse.choices && jsonResponse.choices[0].message) {
        const correctedText = jsonResponse.choices[0].message.content;
        setCorrectedTranscript(correctedText); // Update the UI with the corrected transcript
      }
    } catch (error) {
      console.error('Error correcting transcript:', error);
    }
  };

  const handleShare = async () => {
    try {
      const mediaStream = await navigator.mediaDevices.getDisplayMedia({
        video: true,
        audio: true, // Make sure both video and audio are captured
      });
      console.log("Media Stream:", mediaStream);
  
      // Use the MediaStreamAudioSourceNode from Web Audio API
      const audioContext = new (window.AudioContext || window.webkitAudioContext)();
      const sourceNode = audioContext.createMediaStreamSource(mediaStream);
      const analyserNode = audioContext.createAnalyser();
      sourceNode.connect(analyserNode);
  
      // Here you can process the audio and pass it to your SpeechRecognition API
      if (videoRef.current) {
        videoRef.current.srcObject = mediaStream;
      }
      
    } catch (error) {
      console.error("Error sharing screen:", error);
    }
  
    if (listening) {
      SpeechRecognition.stopListening(); // Stop listening when sharing starts
    } else {
      SpeechRecognition.startListening({
        continuous: true,
        language: "en-IN"
      }); // Start listening
    }
  };
  

  const handleGenerateAI = async () => {
    const text = manualOrCode || latestTranscript;  // Use the corrected transcript if available
    if (text) {
      const res = await fetch('https://api.openai.com/v1/chat/completions', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${apiKey}`,
        },
        body: JSON.stringify({
          model: 'gpt-3.5-turbo',
          messages: [
            { role: 'system', content: 'you are a helpful assistant' },
            { role: 'user', content: `I am attending an interview on this technology ${technology}. can you explain in detail about this ${text} in 100-150 words in simple English, if possible give a small example. please do not give any opening statements or lead-ins or During the interview, these type of things` }
          ],
          temperature: 0,
          max_tokens: 350,
          stream: true, // Enable streaming
        }),
      });

      const reader = res.body.getReader();
      const decoder = new TextDecoder();
      let done = false;
      let accumulatedText = '';

      while (!done) {
        const { value, done: readerDone } = await reader.read();
        done = readerDone;

        const chunk = decoder.decode(value, { stream: true });
        const lines = chunk.split('\n').filter(line => line.trim() !== '');

        for (let line of lines) {
          if (line === '[DONE]') {
            done = true;
            break;
          }

          try {
            const json = JSON.parse(line.replace(/^data: /, ''));
            if (json.choices && json.choices[0].delta && json.choices[0].delta.content) {
              const content = json.choices[0].delta.content;
              accumulatedText += content;
              setChatInput(accumulatedText); // Update UI in real-time
            }
          } catch (error) {
            console.error('Error parsing JSON:', error);
          }
        }
      }
    } else {
      console.log("No transcript to send to AI.");
    }
  };
  const handleGenerateCode = async () => {
    setCode(true)
    SpeechRecognition.stopListening();
    const text = manualOrCode || latestTranscript;  // Use the corrected transcript if available
    if (text) {
      const res = await fetch('https://api.openai.com/v1/chat/completions', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${apiKey}`,
        },
        body: JSON.stringify({
          model: 'gpt-3.5-turbo',
          messages: [
            { role: 'system', content: 'you are a helpful assistant' },
            { role: 'user', content: `I am attending an interview on this technology ${technology}. Please give me simple and perfect code snippet related to ${text}, please do not give any opening statements or lead-ins or any comments, these type of things.` }
          ],
          temperature: 0,
          max_tokens: 500,
          stream: true, // Enable streaming
        }),
      });
  
      const reader = res.body.getReader();
      const decoder = new TextDecoder();
      let done = false;
      let accumulatedText = '';
  
      while (!done) {
        const { value, done: readerDone } = await reader.read();
        done = readerDone;
  
        const chunk = decoder.decode(value, { stream: true });
        const lines = chunk.split('\n').filter(line => line.trim() !== '');
  
        for (let line of lines) {
          if (line === '[DONE]') {
            done = true;
            break;
          }
  
          try {
            const json = JSON.parse(line.replace(/^data: /, ''));
            if (json.choices && json.choices[0].delta && json.choices[0].delta.content) {
              const content = json.choices[0].delta.content;
              accumulatedText += content;
              // Wrap the content in code block format
              const formattedText = `\`\`\`javascript\n${accumulatedText}\n\`\`\``;
              setChatInput(formattedText); // Update UI with formatted code
            }
          } catch (error) {
            console.error('Error parsing JSON:', error);
          }
        }
      }
    } else {
      console.log("No transcript to send to AI.");
    }
  };
  

  const handleTextSelect = (e) => {
    const selected = window.getSelection().toString();
    console.log('Selected Text:', selected);
  };

  const handleSignOut = () => {
    const user = localStorage.getItem('userInfo');
    if(user){
       localStorage.removeItem('userInfo')
  // Get reference to the specific path
       navigate("/login")
    }
}

  return (
    <div style={styles.container}>
      {/* Left Section */}
      

      <div style={styles.leftSection}>
        <div style={styles.header}>Interviewer says:</div>
        <div style={styles.contentBox}>
          <div>
            <input
              type="text"
              onChange={(e) => setTechnology(e.target.value)}
              placeholder="Enter Technology"
              style={styles.textInput}
            />
          </div>
          <video
            ref={videoRef}
            autoPlay
            muted
            style={{ width: '30vw', marginTop: '10px', border: '1px solid black' }}
          />
          <div style={styles.card}>
            <p>Connect to your interview meeting room</p>
            <button style={styles.button} onClick={handleShare}>
              Start Screen Share
            </button>
            <input
              type="text"
              value={manualOrCode}
              onChange={(e) => setManualOrCode(e.target.value)}
              placeholder="For manual search or Code"
              style={styles.textInput}
            />
          </div>
          <div>
            <h3>Questions:</h3>
            <div style={styles.questionsList}>
              <div onMouseUp={handleTextSelect}>
                <span>{transcript}</span>
              </div>
            </div>
          </div>

         
            <div>
              {/* <h4>Latest Transcript:</h4> */}
              {/* <p style={styles.note}>{latestTranscript}</p> */}
              <div style={{ display: 'flex', gap: '20px' }}>
                {/* <button style={styles.button} onClick={handleGenerateAI}>
                  Answer
                </button> */}
                <button style={{background:"red",...styles.button}}  onClick={() => {SpeechRecognition.stopListening(); handleGenerateAI(); setManualOrCode('')}}>
                  Stop and Get Answer
                </button>
                <button style={{background:"green",...styles.button}} onClick={() => { resetTranscript(); SpeechRecognition.startListening({ continuous: true, language: "en-IN" }); setManualOrCode(''); setCode(false) }}>
                  Start Listening
                </button>

                <button style={styles.button} onClick={() => handleGenerateCode()}>
                 Code
                </button>
              </div>
            </div>
          
        </div>
      </div>

      {/* Right Section */}
      <div style={styles.rightSection}>
      <button onClick={handleSignOut} style={styles.button}>Sign Out</button>
        <div style={styles.header}>AI Responses:</div>
        <div style={styles.contentBox}>
          <div style={code ? {...styles.codeBlock, ...styles.javascriptCode} : styles.status}>{chatInput}</div>
        </div>
      </div>
    </div>
  );
};

const styles = {
  container: {
    display: 'flex',
    flexDirection: 'row',
    height: '100vh',
    backgroundColor: '#f5f5f5',
  },
  leftSection: {
    flex: 1,
    display: 'flex',
    flexDirection: 'column',
    padding: '20px',
    backgroundColor: '#ffffff',
    borderRight: '1px solid #ddd',
    overflowY: 'auto',
    position: 'relative',
  },
  rightSection: {
    flex: 1,
    display: 'flex',
    flexDirection: 'column',
    padding: '20px',
    backgroundColor: '#f9f9f9',
    overflowY: 'auto',
  },
  header: {
    fontWeight: 'bold',
    marginBottom: '10px',
    color: '#333',
  },
  contentBox: {
    flex: 1,
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    textAlign: 'center',
    border: '1px solid #ddd',
    borderRadius: '8px',
    padding: '20px',
    backgroundColor: '#fff',
  },
  card: {
    marginBottom: '20px',
  },
  button: {
    padding: '10px 20px',
    backgroundColor: '#ff6f00',
    color: '#fff',
    border: 'none',
    borderRadius: '5px',
    cursor: 'pointer',
    fontSize: '16px',
  },
  textInput: {
    padding: '10px',
    marginTop: '10px',
    width: '90%',
    fontSize: '16px',
    borderRadius: '5px',
    border: '1px solid #ddd',
  },
  note: {
    fontSize: '14px',
    color: '#666',
    marginTop: '10px',
  },
  status: {
    fontSize: '16px',
    color: '#333',
  },
  questionsList: {
    maxHeight: '300px',
    overflowY: 'scroll',
    margin: '20px 0',
    paddingRight: '10px',
  },

  codeBlock: {
    backgroundColor: '#f7f7f7', // Light background color for code block
    border: '1px solid #ddd', // Light border for separation
    borderRadius: '5px', // Rounded corners for the code block
    padding: '15px', // Add padding to give space around the code
    fontFamily: '"Courier New", monospace', // Monospace font for code
    fontSize: '14px', // Smaller font size to match typical code font size
    color: '#333', // Dark color for the code text
    overflowX: 'auto', // Horizontal scroll for long code lines
    whiteSpace: 'pre-wrap', // Maintain line breaks
    wordWrap: 'break-word', // Break long words if necessary
    marginTop: '15px', // Add margin to separate from other content
    textAlign:'left'
  },

  // Optional: Style for JavaScript or specific code language (if needed)
  javascriptCode: {
    borderLeft: '4px solid #ff6f00', // Highlight JavaScript code with an orange left border
    paddingLeft: '12px', // Space for the left border
  },
  
};

export default InterviewLayout;
